  overflow: hidden;
  position: relative;
}
.vux-slider > .vux-swiper > .vux-swiper-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.vux-slider > .vux-swiper > .vux-swiper-item > a {
  display: block;
  width: 100%;
  height: 100%;
}
.vux-slider > .vux-swiper > .vux-swiper-item > a > .vux-img {
  display: block;
  width: 100%;
